import { ShowMoreButton } from "@rp/react/web/common/composite-components"
import { getGroupFamilies } from "../hooks/DiscoveryPanel.helpers"
import { GenericGroupProps } from "./GenericGroup"
import { GroupFamily } from "./GroupFamily"

const MAX_INITIAL_FILTERS = 5
const MAX_TOTAL_FILTERS = 100

const ShowMoreFamilies = ({
  widgetCategory,
  onChange,
  onClick,
  onExclude,
  widgetActions,
  disableCheckbox,
  disableWatchlist,
  onAddToWatchlist,
}: GenericGroupProps) => {
  const { showMoreFamily } = widgetCategory

  const handleShowMore = () => {
    widgetActions.toggleShowMoreFamily(widgetCategory.id)
  }
  const groups = getGroupFamilies(widgetCategory.filters, widgetCategory.groupBy)
  const showMoreItems = groups.slice(MAX_INITIAL_FILTERS, MAX_TOTAL_FILTERS)
  return (
    <>
      {groups.slice(0, MAX_INITIAL_FILTERS).map(({ label, family, filters }) => (
        <GroupFamily
          widgetCategory={widgetCategory}
          widgetActions={widgetActions}
          label={label}
          key={family}
          filters={filters}
          onChange={onChange}
          onClick={onClick}
          disableCheckbox={disableCheckbox}
          disableWatchlist={disableWatchlist}
          onAddToWatchlist={onAddToWatchlist}
          onExclude={onExclude}
        />
      ))}
      {showMoreFamily
        ? groups
            .slice(MAX_INITIAL_FILTERS, MAX_TOTAL_FILTERS)
            .map(({ label, family, filters }) => (
              <GroupFamily
                widgetCategory={widgetCategory}
                widgetActions={widgetActions}
                label={label}
                key={family}
                filters={filters}
                onChange={onChange}
                onClick={onClick}
                disableCheckbox={disableCheckbox}
                disableWatchlist={disableWatchlist}
                onAddToWatchlist={onAddToWatchlist}
                onExclude={onExclude}
              />
            ))
        : null}
      {groups.length > MAX_INITIAL_FILTERS ? (
        <ShowMoreButton
          isFamilyButton
          showMore={showMoreFamily}
          onClick={handleShowMore}
          qty={showMoreItems.length}
        />
      ) : null}
    </>
  )
}

export { ShowMoreFamilies }
